import React, { useState } from 'react';
import { useQuery } from 'react-query';
import { financialApi } from './services/api';
import IncomeStatementChart from './components/IncomeStatementChart';
import { IncomeStatementData } from './types';

function App() {
  const [symbol, setSymbol] = useState<string>('AAPL');
  const [searchSymbol, setSearchSymbol] = useState<string>('AAPL');
  const [chartType, setChartType] = useState<string>('all-metrics');

  const { data, isLoading, error, refetch } = useQuery<IncomeStatementData[]>(
    ['incomeStatement', searchSymbol],
    () => financialApi.getIncomeStatement(searchSymbol),
    {
      enabled: !!searchSymbol,
    }
  );

  const handleSearch = () => {
    setSearchSymbol(symbol);
    refetch();
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleSearch();
    }
  };

  return (
    <div className="App">
      <header className="header">
        <h1> </h1>
        <p>     </p>
      </header>

      <div className="container">
        <>
          <div className="search-container">
          <input
            type="text"
            className="search-input"
            placeholder="   (: AAPL)"
            value={symbol}
            onChange={(e) => setSymbol(e.target.value.toUpperCase())}
            onKeyPress={handleKeyPress}
          />
          <button className="search-button" onClick={handleSearch}>
            
          </button>
        </div>

        {isLoading && <div className="loading">   ...</div>}

        {error && (
          <div className="error">
                 .     .
          </div>
        )}

        {data && data.length > 0 && (
          <div>
            <h2>{searchSymbol} </h2>
            <div className="chart-controls">
              <label htmlFor="chart-type-select"> : </label>
              <select
                id="chart-type-select"
                value={chartType}
                onChange={(e) => setChartType(e.target.value)}
                className="chart-type-select"
              >
                <option value="all-metrics"> </option>
                <option value="revenue-trend"> </option>
                <option value="profit-comparison"> </option>
                <option value="profit-margin"> </option>
              </select>
            </div>
            <IncomeStatementChart data={data} chartType={chartType} />
          </div>
        )}

        {data && data.length === 0 && (
          <div className="error">
 {searchSymbol}      .
          </div>
        )}
        </>
      </div>
    </div>
  );
}

export default App;